import { get, getConfig, post, postConfig } from './http.js'

//
export const login = (data) => post('/api/login', data)
//
export const createUserAccount = (data) => post('/api/register', data)
//
export const resendRegisterMail = (data) => post('/api/resend-register-mail', data)
//
export const getMember = (data, config) => getConfig('/api/member-center', data, config)



/*----------API-------------*/
//
export const BNping = (el) => get(el + '/api/v3/ping')

export const BNexchangeInfo = (el) => get('https://api.binance.com/api/v3/exchangeInfo')
